const apiurl = {
  android: "https://a.app.qq.com/o/simple.jsp?pkgname=net.xinhuamm.mainclient",
  ios: "https://a.app.qq.com/o/simple.jsp?pkgname=net.xinhuamm.mainclient",
};
// 复制文字到剪贴板，兼容
let copyTextToClipboard = (e) => {
  console.log(e);
  var t = document.createElement("textarea");
  (t.value = e),
    t.setAttribute("readonly", ""),
    (t.style.contain = "strict"),
    (t.style.position = "absolute"),
    (t.style.left = "-9999px"),
    (t.style.fontSize = "12pt");
  var n = document.getSelection(),
    r = !1;
  n.rangeCount > 0 && (r = n.getRangeAt(0)),
    document.body.appendChild(t),
    t.select(),
    (t.selectionStart = 0),
    (t.selectionEnd = e.length);
  var o = !1;
  try {
    o = document.execCommand("copy");
  } catch (e) {}
  return (
    document.body.removeChild(t), r && (n.removeAllRanges(), n.addRange(r)), o
  );
};

//获取app版本号
const getAppVersion = function (ua) {
  let versionMatch = ua.match(/appVersion:\d+\.\d+\.\d+/);
  let versionStr = versionMatch && versionMatch[0];
  if (!!versionStr) {
    return versionStr.split(":")[1];
  } else {
    return "";
  }
};

//判断设备系统
let versions = (function () {
  let u = navigator.userAgent,
    app = navigator.appVersion;
  let appV = getAppVersion(u);
  return {
    trident: u.indexOf("Trident") > -1,
    presto: u.indexOf("Presto") > -1,
    webKit: u.indexOf("AppleWebKit") > -1,
    gecko: u.indexOf("Gecko") > -1 && u.indexOf("KHTML") === -1,
    mobile: !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/),
    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
    android: u.indexOf("Android") > -1 || u.indexOf("Linux") > -1,
    iPhone: u.indexOf("iPhone") > -1 || u.indexOf("Mac") > -1,
    iPad: u.indexOf("iPad") > -1,
    webApp: u.indexOf("Safari") === -1,
    weixin: u.match(/micromessenger/i),
    appVersion: appV,
    xyapp: u.indexOf("XinhuasheBrower") > -1,
    qq: u.indexOf("QQ/") > -1,
    qqBrowser: u.indexOf("MQQBrowser") > -1,
    wechat: u.indexOf("MicroMessenger/") > -1,
  };
})();

//拼接url query参数
let spliceQuery = (querys) => {
  if (typeof querys == "object") {
    let urlquery = "";
    for (let key in querys) {
      urlquery += key + "=" + querys[key] + "&";
    }
    return urlquery.slice(0, urlquery.length - 1);
  }
};

let combineAppLink = (name, obj) => {
  let url = "xhpfm://" + name + (!!obj ? "/?" + spliceQuery(obj) : "");
  let linkUrl =
    location.href.indexOf("test") > -1
      ? "https://testuniversallink.zhongguowangshi.com"
      : "https://d.zhongguowangshi.com";
  let userAgent = window.navigator.userAgent.toLocaleLowerCase();
  let isWx = !!userAgent.indexOf("micromessenger") > -1;
  copyTextToClipboard(url);

  if (versions.ios || versions.iphone || versions.iPad) {
    let start = userAgent.indexOf("os ");
    let versions = +userAgent.substr(start + 3, 3).replace("_", ".");
    if (versions >= 9) {
      return (
        linkUrl +
        "?url=" +
        "xhpfm://" +
        name +
        (!!obj ? "/?" + spliceQuery(obj) : "")
      );
    } else if (isWx) {
      return (
        apiurl.ios +
        "&ios_schema=" +
        encodeURIComponent(
          "xhpfm://" + name + (!!obj ? "/?" + spliceQuery(obj) : "")
        )
      );
    } else {
      return (
        linkUrl +
        "?url=" +
        "xhpfm://" +
        name +
        (!!obj ? "/?" + spliceQuery(obj) : "")
      );
    }
  } else if (versions.android) {
    return (
      apiurl.android +
      "&android_schema=" +
      encodeURIComponent(
        "xhpfm://" + name + (!!obj ? "/?" + spliceQuery(obj) : "")
      )
    );
  } else {
    return (
      apiurl.android +
      "&android_schema=" +
      encodeURIComponent(
        "xhpfm://" + name + (!!obj ? "/?" + spliceQuery(obj) : "")
      )
    );
  }
};

export default function invokeXinHuaClient({ path="" }) {
  // 唤起客户端或者跳转下载页
  location.href = combineAppLink('light', {
    id: '9946120', //客户端稿件轻应用id 9946120
    newstype: 1004, //轻应用稿件1004
    link: location.origin + process.env.BASE_URL + path + 'index.html', // h5地址
    title: document.title,
  })
}
